import React from "react";
import "../CSS/home.css";
import img1 from '../IMAGES/Home-pic.png';
import { Link } from "react-router-dom";


const Home = () => {
    return (
        <div>
            <div id="home-container">
                <div id="home-page">
                </div>

                <div className="home-middle-content">
                    <div className="home-me">
                        <p className="home-myName">Hi, I'm<span> Abhi</span></p>
                        <p className="home-role">Fullstack Developer</p>
                        <p className="home-about">A Fullstack focused Web Developer building the Frontend of Websites and Web Applications that leads to the success of overall product.</p>

                        <div className="home-button">
                            <p className="home-about-me"><Link to="/About">About Me</Link></p>
                        </div>
                    </div>

                    <div className="home-png">
                        <img src={img1} class="home-image" />
                    </div>
                </div>

                <div className="home-icons">
                    <a href="https://www.linkedin.com/in/abinesh30" target="_blank"><i className="fa-brands fa-linkedin"></i></a>
                    <a href="https://www.instagram.com/abi_nexx/" target="_blank"><i className="fa-brands fa-instagram"></i></a>
                    <a href="https://www.github.com/Abinex3" target="_blank"><i className="fa-brands fa-github"></i></a>
                </div>
            </div>
        </div>
    );
}

export default Home;
