import React from "react";
import img7 from '../assets/card.jpg';

const Card = (props) => {
    return (
        <div className="emp-flex">
            <div className="emp-container">
                <div className="emp-nav"></div>

                <div>
                    <div className="emp-company">
                        <p className="emp-text">ARCH<br/>OWL</p>
                    </div>

                    {/* <img src="IMAGES/card.jpg"/> */}
                    <img src={img7} />
                </div>

                <div id="emp-name">
                    <p className="emp-name">{props.emp.Name}</p>
                    <i className="fa-solid fa-right-to-bracket" onClick={() => props.btnfn()} id="view-btn">{props.btntext}</i>
                    <p className="emp-id">{props.emp.EmployeeID}</p>
                </div>
            </div>

        </div>
    )
}

export default Card;