import React from 'react';
import "../CSS/calculator.css";
const Calculator = () => {
    const [value, setValue] = React.useState('');
    // const [clear, Calc] = React.
    // let value = 0;
    // function setValue(val) {
    //     value = val;
    //     console.log(value);
    //     document.querySelector("#display p").textContent = value;
    // }
    return (
        <div>
            
            <div id="container">
                <div id="display">
                    <h1>
                    {value}

                    </h1>
                </div>

                <div id="main">
                    <div className="numbers" onClick={() => setValue('')}><span className="div-1">AC</span></div>
                    <div className="numbers" onClick={() => setValue(value.split('').pop(''))}><span className="div-1">C</span></div>
                    <div className="numbers" onClick={() => setValue(value + '%')}><span className="div-1">%</span></div>
                    <div className="numbers" onClick={() => setValue(value + '/')}><span className="div-3">/</span></div>
                </div>


                <div id="main">
                    <div className="numbers" onClick={() => setValue(value + '7')}><span className="div-2">7</span></div>
                    <div className="numbers" onClick={() => setValue(value + '8')}><span className="div-2">8</span></div>
                    <div className="numbers" onClick={() => setValue(value + '9')}><span className="div-2">9</span></div>
                    <div className="numbers"onClick={() => setValue(value + '*')}><span className="div-3">X</span></div>
                </div>

                <div id="main">
                    <div className="numbers" onClick={() => setValue(value + '4')}><span className="div-2">4</span></div>
                    <div className="numbers" onClick={() => setValue(value + '5')}><span className="div-2">5</span></div>
                    <div className="numbers" onClick={() => setValue(value + '6')}><span className="div-2">6</span></div>
                    <div className="numbers" onClick={() => setValue(value + '-')}><span className="div-3">-</span></div>
                </div>

                <div id="main">
                    <div className="numbers" onClick={() => setValue(value + '1')}><span className="div-2">1</span></div>
                    <div className="numbers" onClick={() => setValue(value + '2')}><span className="div-2">2</span></div>
                    <div className="numbers" onClick={() => setValue(value +'3')}><span className="div-2">3</span></div>
                    <div className="numbers" onClick={() => setValue(value + '+')}><span className="div-3" >+</span></div>
                </div>

                <div id="main">
                    <div className="numbers" onClick={() => setValue(value + '.')}><span className="div-2">.</span></div>
                    <div className="numbers" onClick={() => setValue(value + '0')}><span className="div-2">0</span></div>
                    <div className="numbers" onClick={() => setValue(eval(value))}><span className="div-4">=</span></div>
                </div>
            </div>
        </div>
    )
}
export default Calculator;