import React from 'react';
import "../CSS/SignUp.css";
const Signup = () => {
    return (


    <div id="signup-main-container">

      <div id="signup-container">

        <form action="" className="signup-form">

            <div className="signup-heading">
                 <p className="signup-title">Sign Up</p>
            </div>

            <div className="signup-input">
                <input type="text" placeholder="First Name" className="signup-first-name"/>
                <input type="text" placeholder="Last Name" className="signup-last-name"/><br/>
                <input type="email" placeholder="Enter Your Email" className="signup-email"/><br/>
                <input type="password" placeholder="password" className="signup-password"/><br/>
                <input type="password" placeholder="Confirm Password" className="signup-confirm-password"/><br/>
            </div>
            <div className="signup-privacy-policy">
                <input type="checkbox"/>
                <p className="signup-policy">I Agree with<span className="signup-color"> privacy</span> and<span className="signup-color"> policy</span></p>
            </div>

            <div className="signup-button">
                <button className="signup-button1">Sign up</button>
            </div>

        </form>

      </div>

    </div>
 
    )
}

export default Signup;