import React from "react";
import "../CSS/login.css";
import {Link} from 'react-router-dom';
import { useForm } from "react-hook-form";
import Swal from "sweetalert2";

const Login = () => {

    const { register, handleSubmit, formState:{errors} } = useForm();

    const onSubmit = (data) => {
        console.log(data);
        // Swal.world('Hello world');
    }

    return (
        <div>
            <div id="main-container">

                <div id="login-container">

                    <form onSubmit={handleSubmit(onSubmit)} className="login-form">

                        <div className="login-heading">
                            <p className="login-title">Login</p>
                            <div className="sub-log">
                                <p className="login-sub">Hey, Enter details to get sign in to your account</p>
                            </div>
                        </div>

                        <div className="login-input">
                            <input type="type" placeholder="Email here" className="login-email" {...register('username', {
                                required: {
                                    value: true,
                                    message: "Email is required"
                                },
                                required: {
                                    value:/^[\w-\.]+@([\w-]+\.)+[\w-]{2,4}$/,
                                    message: "Enter a valid email"
                                }
                            })} />

                            <div>
                                <p>{errors.username?.message}</p>
                            </div>

                            <br />
                            <input type="password" placeholder="Passcode" className="login-password" {...register('login-password')} />
                            <p className="login-forgotPassword">Forgot your passcode?</p>
                        </div>



                        <div className="login-button">
                            <button className="login-button1">Sign in</button>
                        </div>


                        <p className="or-login-title">-Or Sign in with</p>
                        <div className="other-logins">
                            <div className="other-login-logo">
                                <i className="fa-brands fa-google"></i>
                                <p className="login-links">Google</p>
                            </div>

                            <div className="other-login-logo">
                                <i className="fa-brands fa-apple"></i>
                                <p className="login-links">Apple ID</p>
                            </div>

                            <div className="other-login-logo">
                                <i className="fa-brands fa-facebook"></i>
                                <p className="login-links">Facebook</p>
                            </div>
                        </div>
                        <div className="login-dont">
                            <p className="login-signup">Don't have an account?<a href="/Signup" className="signup-link"> Request Now</a></p>
                        </div>
                    </form>
                </div>
            </div>
        </div>
    )
}
export default Login;