import React from "react";
import "../CSS/about-me.css";
import img2 from '../IMAGES/about-me.png';
import img3 from '../IMAGES/HTML (2).png';
import img4 from  '../IMAGES/CSS.png';
import img5 from '../IMAGES/JS.png';
import img6 from '../IMAGES/REACT.png';


const About = () => {
    return (
        <div>
            <div id="about-me">
                <div className="about-me-img">
                    <img src={img2} className="my-img"/>
                </div>

                <div className="about-me-details">
                    <p className="about-me-name">About<span className="me-color"> Me</span></p>
                    <p className="about-me-sub">Motivated junior web developer looking for a role as full-stack web developer at SoftwareX. Passionate about building first-class web applications. Developed an e-commerce website for a local fashion retailer while at University X. Relevant skills include DNS Management, Database, Debugging, and more.</p>

                    <div className="Details">
                        <div className="about-me-moreDetails">
                            <p className="about-me-moreDetails-1">Name</p>
                            <p className="about-me-moreDetails-1">Age</p>
                            <p className="about-me-moreDetails-1">Address</p>
                            <p className="about-me-moreDetails-1">Phone</p>
                            <p className="about-me-moreDetails-1">E-mail</p>
                        </div>

                        <div className="about-me-colon">
                            <p className="about-me-colon">:</p>
                            <p className="about-me-colon">:</p>
                            <p className="about-me-colon">:</p>
                            <p className="about-me-colon">:</p>
                            <p className="about-me-colon">:</p>
                        </div>

                        <div className="about-me-answers">
                            <p className="about-me-answers">Abinesh M</p>
                            <p className="about-me-answers">20</p>
                            <p className="about-me-answers">Velacherry, Chennai</p>
                            <p className="about-me-answers">9586958495</p>
                            <p className="about-me-answers">rohithabi30@gmail.com</p>
                        </div>
                    </div>
                    <div className="my-skills">
                        <p className="skills-are">My<span className="skills"> Skills</span></p>
                        <div className="about-me-skills">
                            <img src={img3} className="html"/>
                            <img src={img4} className="css"/>
                            <img src={img5} className="js"/>
                            <img src={img6} className="react"/>
                        </div>
                    </div>
                    <p className="my-cv"> Download CV</p>
                </div>

            </div>
        </div>
    )
}

export default About;
